import axios from '@axios'

export default [
  // {
  //   path: '/hostigamiento/encuesta',
  //   name: 'hostigamiento.encuesta.index',
  //   component: () => import('@/views/servicio-social/hostigamiento/encuesta/Formulario.vue'),
  //   meta: {
  //     layout: 'full',
  //   },
  // },
  // {
  //   path: '/hostigamiento/denunciar',
  //   name: 'hostigamiento.denunciar.index',
  //   component: () => import('@/views/servicio-social/hostigamiento/denuncia/FormularioRegistroDenuncia.vue'),
  //   meta: {
  //     layout: 'full',
  //   },
  // },
  {
    path: '/hostigamiento/denuncias/buzon',
    name: 'social-buzon-denuncias',
    component: () => import('@/views/servicio-social/hostigamiento/buzon-denuncia/BuzonDenunciasList.vue'),
    meta: {
      pageTitle: 'Gestin de denuncias',
      breadcrumb: [
        {
          text: 'Buzn',
          active: true,
        },
      ],
    },
  },
  {
    path: '/hostigamiento/denuncias',
    name: 'social-denuncias',
    component: () => import('@/views/servicio-social/hostigamiento/denuncia/DenunciasList.vue'),
    meta: {
      pageTitle: 'Gestin de denuncias',
      breadcrumb: [
        {
          text: 'Denuncias',
          active: true,
        },
      ],
    },
  },
  {
    path: '/hostigamiento/denuncias/reportes',
    name: 'social-denuncias-reportes',
    component: () => import('@/views/servicio-social/hostigamiento/reportes/ReportesHostigamiento.vue'),
  },
  {
    path: '/hostigamiento/denuncias/respuestas',
    name: 'respuestas-denuncias-hostigamiento',
    component: () => import('@/views/servicio-social/hostigamiento/denuncia/respuestas/RespuestasDenunciasHostigamiento.vue'),
  },
  {
    path: '/f/servicio-social/prevencion-hostigamiento/denuncias/:idDenuncia/precalificar_pdf',
    name: 'download-PDF-precalificar',
    // component: () => import('@/views/servicio-social/hostigamiento/reportes/IndicadorTasaCrecimientoDenuncias.vue'),
    // eslint-disable-next-line no-unused-vars
    beforeEnter: (to, from, next) => {
      // console.log(to)
      console.log('RUTA ENVIAR:', `${window.location.host}${to.path}`)

      axios({
        url: `/servicio-social/prevencion-hostigamiento/denuncias/${to.params.idDenuncia}/precalificar_pdf`,
        method: 'GET',
        params: { url_pdf: `${window.location.host}${to.path}` },
        responseType: 'blob', // important
      })
        .then(response => {
          const url = window.URL.createObjectURL(new Blob([response.data]))
          const link = document.createElement('a')
          // link.setAttribute('href', `data:text/plain;charset=utf-8,${encodeURIComponent(response.data)}`)

          // link.style.display = 'none'
          link.href = url
          link.setAttribute('download', 'file.pdf')
          // link.setAttribute('target', '_blank')

          document.body.appendChild(link)
          link.click()

          // document.body.removeChild(link)
        })
    },
  },

]
