import axios from '@axios'
// import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

export default [
  {
    path: '/listar-formularios',
    name: 'listar-formularios-index',
    component: () => import('@/views/administrador/gestion-formulario/listarFormularios.vue'),
    props: true,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: '/recurso/formulario/:id_formulariox',
    name: 'formulario-x',
    component: () => import('@/views/administrador/gestion-formulario/formulario/Formulario.vue'),
    props: true,
    meta: {
      requiresAuth: true,
    },
    beforeEnter: (to, from, next) => {
      axios.get(`/recursos/formulario/${to.params.id_formulariox}`)
        .then(res => (res.data.autentificacion === 1 ? next() : next({ name: 'vista-anonima-x', params: { id_formulariox: to.params.id_formulariox } })))
        .catch(() => {
          // this.$toast({
          //   component: ToastificationContent,
          //   position: 'top-right',
          //   props: {
          //     title: 'Error!',
          //     text: 'El formulario no existe',
          //     icon: 'AlertCircleIcon',
          //     variant: 'danger',
          //   },
          // })
          next({ name: 'listar-formularios-index' })
        })
    },
  },
  {
    path: '/recurso/formulario/anonimo/:id_formulariox',
    name: 'vista-anonima-x',
    component: () => import('@/views/administrador/gestion-formulario/vista-anonima/VistaAnonima.vue'),
    meta: {
      layout: 'full', // linea para ocultar el menu y cabecera de usuario
      requiresAuth: false,
      // redirectIfLoggedIn: true,
      // resource: 'api',
      // action: 'salud.triajes.index',
    },
    props: true,
    beforeEnter: (to, from, next) => {
      axios.get(`/recursos/formulario/${to.params.id_formulariox}`)
        .then(res => (res.data.autentificacion === 0 ? next() : next({ name: 'formulario-x', params: { id_formulariox: to.params.id_formulariox } })))
        .catch(() => {
          // this.$toast({
          //   component: ToastificationContent,
          //   position: 'top-right',
          //   props: {
          //     title: 'Error!',
          //     text: 'El formulario no existe',
          //     icon: 'AlertCircleIcon',
          //     variant: 'danger',
          //   },
          // })
          next({ name: 'listar-formularios-index' })
        })
    },
  },
  {
    path: '/nuevo-formulario/:idFormulario',
    name: 'nuevo-formulario-index',
    component: () => import('@/views/administrador/gestion-formulario/nuevo-formulario/newFormulario.vue'),
    props: true,
    meta: {
      requiresAuth: true,
    },
  },
  // {
  //   path: '/nuevo-formulario-prueba',
  //   name: 'nuevo-formulario-indexaa',
  //   component: () => import('@/views/administrador/gestion-formulario/nuevo-formulario/nuevoFormulario.vue'),
  //   props: true,
  // },
]
