import axios from '@axios'

export default [
  {
    path: '/asistencia-social/soporte',
    name: 'social-soporte-list',
    component: () => import('@/views/social/soporte-social/SoporteSocialList.vue'),
    meta: {
      pageTitle: 'Gestin de asistencia social',
      breadcrumb: [
        {
          text: 'Soporte social',
          active: true,
        },
      ],
      requiresAuth: true,
      action: 'social.asistencia.soporte.index',
      resource: 'api',
    },
  },
  {
    path: '/servicio-social/soporte-social/:id/pdf',
    name: 'download-pdf-soporte-social',
    beforeEnter: (to, from, next) => {
      axios({
        url: `/servicio-social/asistencia-social/soporte/${to.params.id}/pdf`,
        method: 'GET',
        params: { url_pdf: `${window.location.host}${to.path}` },
        responseType: 'blob', // important
      })
        .then(response => {
          const url = window.URL.createObjectURL(new Blob([response.data]))
          const link = document.createElement('a')
          link.href = url
          link.setAttribute('download', `SOPORTE SOCIAL - COD-${to.params.id}.pdf`)
          document.body.appendChild(link)
          link.click()
        })
      next(from)
    },
    meta: {
      requiresAuth: true,
      action: 'social.asistencia.soporte.index',
      resource: 'api',
    },
  },
  // {
  //   path: '/asistencia-social/denuncias/pre-calificar',
  //   name: 'social-asistencias-calificar',
  //   component: () => import('@/views/servicio-social/asistencia-social/asistencias-list/AsistenciaSocialPreCalificar.vue'),
  // },
]
